@import url('https://fonts.googleapis.com/css?family=Raleway:500|Lalezar:400&display=swap');
@import url("https://use.typekit.net/nwr1tma.css"); 

@keyframes cardShuffleIn {
  0% {
    transform: translateY(100vh);
    opacity: 0;
  }

  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes picExpand {
  0% {
    transform: translateX(200px);
  }

  100% {
    transform: translateX(0px);
  }
}

* {
  margin: 0;
  padding: 0;
  color: #00eeff;

  --cyan: #00eeff
}

html {
  font-size: 10px;
}

body {
  background: hsl(218, 100%, 5%);
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

header {
  width: 100%;

  display: flex;
  justify-content: space-between;
  align-items: center;

  margin-bottom: 1.5rem;
}

.headerText {
  margin: 1rem;
}

.wkndTitle {
  font-family: 'bebas-neue', sans-serif;
  font-size: 4rem;
  word-spacing: 1px;
  color: white;
}

.dates {
  color: var(--cyan);
  font-size: 2rem;
  font-family: 'Raleway', sans-serif;
}

.profileImg {
  margin: 1rem 2rem;
  width: 6rem;
  height: 6rem;
  background-color: var(--cyan);
  border-radius: 100%;
  animation: picExpand .5s cubic-bezier(0.075, 0.82, 0.165, 1) .9s backwards;
}

.card {
  display: flex;
  flex-direction: column;

  width: 95vw;
  max-height: 20%;
  margin: 0 1rem 1rem 1rem;

  border-radius: 5px;
  background: rgb(12, 22, 38);
  animation: cardShuffleIn 1s cubic-bezier(0.075, 0.82, 0.165, 1) both;
}

#SAT {
  animation-delay: .2s;
}

#SUN {
  animation-delay: .4s;
}

.cardHeader {
  display: flex;
  justify-content: space-between;
  align-content: flex-start;
  padding: 0.5rem 1rem;
}

.day {
  font-family: 'bebas-neue', sans-serif;
  color: var(--cyan);
  font-size: 5rem;
  letter-spacing: 3pt;
}

.ellipses {
  width: .7rem;
  height: .7rem;
  align-self: center;
  margin-right: 10%;
  background-color: var(--cyan);
  border: none;
  border-radius: 1rem;
}

.ellipses::before, .ellipses::after {
  content: '';
  position: relative;
  display: inline-block;
  width: inherit;
  height: inherit;
  background-color: inherit;
  border-radius: inherit;
}

.ellipses::before {
  right: 2rem;
  top: -.3rem;
}

.ellipses::after {
  left: 2rem;
  top: -2rem;
}

.activities {
  display: flex;
  justify-content: space-between;
  margin: 1rem 2rem;
}

.icon {
  width: 15vw;
  height: 15vw;
  background-color: transparent;
  border: 3px dotted yellow;
  box-shadow: 0px 0px 20px -10px yellow, inset 0px 0px 20px -10px yellow;
}

.movie {
  border-color: rgb(82, 255, 2);
  box-shadow: 0px 0px 20px -10px rgb(82, 255, 2), inset 0px 0px 20px -10px rgb(82, 255, 2);
}

.dnd {
  border-color: rgb(255, 0, 43);
  box-shadow: 0px 0px 20px -10px rgb(255, 0, 43), inset 0px 0px 20px -10px rgb(255, 0, 43);
}

.cardFooter {
  display: flex;
  justify-content: space-between;
  margin: 1rem 2rem;
  font-family: 'Bebas Neue', sans-serif;
  font-size: 2rem;
}

.cardFooter p:first-child {
  color: red;
}

.cardFooter p:last-child {
  color: rgb(0, 255, 0);
}

.spacing {
  width: 100%;
  height: 10px;
}